import React from 'react';
import { Form, Icon, Input, Button, Checkbox, Layout, Card } from 'antd';

const Loading = (props) => {
    return (
        <Layout
            style={{
                height: '100vh',
                marginTop: 250,
                alignItems: 'center',
                background: "#FFF"
            }}
        >
            <Icon type="loading" style={{ fontSize: 80 }} />
            <p style={{ fontSize: 20, marginTop: 20 }}>Loading.......</p>
        </Layout >
    )
}
export default Loading